<template>
	<view class="content">
		<!-- 头部轮播 -->
		<view class="carousel-section">
			<swiper 
				class="carousel" 
				:indicator-dots="indicatorDots" 
				:autoplay="autoplay" 
				:interval="interval" 
				:duration="duration" 
				indicator-active-color="#FFFFFF" 
				@change="swiperChange">
				<swiper-item 
					v-for="(item, index) in carouselList" 
					:key="index" 
					class="carousel-item">
					<image :src="item.src" mode="widthFix" />
				</swiper-item>
			</swiper>
		</view>
		<view class="content-wrapper">
			<!-- 我的待办 -->
			<view class="notice-header">
				<h2>我的待办</h2>
				<view class="view-more" @tap="navigateTo('/pages/todoList/todoList')">
					共{{total}}条 <i class="iconfont icon-jiantou2"></i>
				</view>
			</view>
			<view class="notice-section">
				<view class="notice-list" v-if="!isEmpty">
					<view 
						v-for="(item, index) in todoList" 
						:key="item.dataId"
						class="notice-item"
						@click="navToDetailPage(item)">
						<i class="icon-dot"></i>
						<text class="text text-ellipsis">{{item.title}}</text>
					</view>
				</view>
				<view v-else class="notice-item">
					<i class="icon-dot"></i>
					<text class="text ">暂无待办</text>
				</view>
			</view>
			
			<!-- 通知公告 -->
			<view class="notice-header">
				<h2>通知公告</h2>
				<view class="view-more">
					更多 <i class="iconfont icon-jiantou2"></i>
				</view>
			</view>
			<view class="notice-section notice-section1">
				<view class="notice-list">
					<view 
						v-for="(item, index) in newsList" 
						:key="item.id"
						class="notice-item"
						@click="navToDetailPage(item)">
						<text class="text text-ellipsis">{{item.text}}</text>
					</view>
				</view>
			</view>
			
			<!-- 广告区域 -->
			<view class="ada-section">
				<image src="../../static/images/ada.png" mode="widthFix" />
			</view>
			<!-- 底部区域 -->
			<view class="bottom-section">
				<view class="section-item" @tap="navigateTo('/pages/sign/sign')">
					<image src="../../static/images/bottom-img-1.png" mode="widthFix" />
				</view>
				<view class="section-item" @tap="navigateTo('/pages/evaluate/evaluate')">
					<image src="../../static/images/bottom-img-2.png" mode="widthFix" />
				</view>
				<view class="section-item" @tap="navigateTo('/pages/share/share')">
					<image src="../../static/images/bottom-img-3.png" mode="widthFix" />
				</view>
				<view class="section-item">
					<image src="../../static/images/bottom-img-4.png" mode="widthFix" />
				</view>
			</view>
		</view>
		<view class="mask-wrapper" v-if="showPopup">
			<view class="mask-bg"></view>
			<view class="mask-content">
				<image src="../../static/images/birthday.png" mode="widthFix"></image>
				<view class="popup-close" @tap="closePopup">
					<image src="../../static/images/popup-close.png" mode="widthFix"></image>
				</view>
				<view class="main-text">
					<view class="title">天翼祝你政治生日快乐</view>
					<view class="name">{{birthdayName}}同志：</view>
					<view class="desc">今天是您的入党纪念日，党组织向您表示热烈的祝贺。
					希望您践行入党誓词，永葆共产党的先进本色。同时祝您身体健康，生活愉快！</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				showPopup: false,
				birthdayName: '',
				carouselList: [{
					src: "/static/images/banner.png",
				}, {
					src: "/static/images/banner.png",
				}, {
					src: "/static/images/banner.png",
				}],
				indicatorDots: true,
				autoplay: true,
				interval: 2000,
				duration: 500,
				todoList: [],
				total: 0,
				newsList: [{
					id: '01',
					text: '2021-05-08 三会一课总结审核'
				}, {
					id: '02',
					text: '2021-05-07 三会一课请假审核'
				}],
				isEmpty: true
			}
		},
		onLoad() {
			this.queryTodoList();
			this.queryBirthday();
		},
		methods: {
			// 下拉刷新
			onPullDownRefresh() {
				setTimeout(() => {
					uni.stopPullDownRefresh(); // 停止当前页面下拉刷新
					this.queryTodoList();
				}, 1000);
			},
			async queryTodoList() {
				const { data } = await this.$http({
					url: '/todo/list',
					method: 'POST',
					data: {
						pageNum: this.pageNum,
						pageSize: 5,
						reqFlag: 1
					}
				});
				this.total = data.data.total;
				if(data.data.rows.length === 0) {
					this.isEmpty = true;
				} else {
					this.isEmpty = false;
					if(data.data.rows.length > 2 )  {
						this.todoList = data.data.rows.slice(0,2);
					} else {
						this.todoList = data.data.rows;
					}				
				}
			},
			onNavigationBarButtonTap() {
				uni.scanCode({
					success: res => {
						this.$http({
							url: '/checkin/scanSign',
							data: {
								codeInfo: res.result
							},
							method: 'GET',
							header: {
								"Content-type": "application/x-www-form-urlencoded"
							},
							sslVerify: false,
						}).then(res => {
							if (res.data.msg) {
								uni.navigateTo({
									url: '/pages/signSuccess/signSuccess'
								});
							} else {
								uni.navigateTo({
									url: '/pages/signFail/signFail'
								});
							}
						})
					}
				}); 
			},
			async queryBirthday() {
				const { data } = await this.$http({
					url: '/checkin/politicBirth',
					method: 'GET',
				});
				this.birthdayName = data.data;
				if(data.msg === '1') {
					this.showPopup = true;
				}
			},
			navigateTo(url) {
				uni.navigateTo({
					url: url
				});
			},
			swiperChange(e) {
				//console.log(e.target.value);
			},
			navToDetailPage(item) {
				//console.log(item);
			},
			closePopup() {
				this.showPopup = false;
			}
		}
	}
</script>

<style lang="scss">
.carousel {
	width: 100%;
	.carousel-item {
		width: 100%;
		height: 100%;
		overflow: hidden;
	}
}
.content-wrapper {
	padding: 30upx;
	.notice-section {
		width: 100%;
		margin-bottom: 40upx;
		padding: 30upx;
		box-shadow: 0px 0px 6px rgba(219, 219, 219, 0.5);
	}
	.notice-section1 {
		border-top: 1upx solid #e7eaf0;
		box-shadow: none;
		padding: 30upx 0;
	}
	.notice-header {
		display: flex;
		justify-content: space-between;
		height: 82upx;
		align-items: center;
		h2 {
			font-size: 30upx;
			font-weight: bold;
		}
		.view-more {
			font-size: 28upx;
			color: #aaa;
		}
	}
	.notice-item {
		font-size: 28upx;
		line-height: 38upx;
		color: #434955;
		margin-bottom: 10upx;
		display: flex;
		align-items: center;
		.icon-dot {
			display: inline-block;
			width: 10upx;
			height: 10upx;
			border-radius: 50%;
			background: #434955;
			margin-right: 10upx;
		}
		.text {
			flex: 1;
		}
		&.visited {
			color: #7b7a7a;
			.icon-dot { 
				background: #7b7a7a;
			}
		}
	}
	.ada-section {
		width: 100%;
		height: 100%;
		margin-bottom: 40upx;
	}
	.bottom-section {
		display: flex;
		flex-wrap: wrap;
		.section-item {
			width: 48%;
			margin-bottom: 20upx;
			&:nth-child(2n+1) {
				margin-right: 4%;
			}
		}
	}
}
.mask-bg {
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	z-index: 1;
	background-color: rgba(0, 0, 0, 0.6);
}
.mask-content {
	position: fixed;
	left: 50%;
	top: 46%;
	z-index: 2;
	margin: -420upx 0 0 -300upx;
	width: 600upx;
	height: 837upx;
	.popup-close {
		position: absolute;
		top: 48upx;
		right: 0;
		width: 48upx;
		height: 48upx;
	}
	.main-text {
		position: absolute;
		top: 374upx;
		left: 50upx;
		right: 50upx;
		color: #ffebd0;
		line-height: 53upx;
		font-size: 14px;
		text-shadow: 2px 3px 3px rgba(255, 42, 19, 0.5);
		.title {
			font: bold 24px/108upx '宋体', Arial;
			text-align: center;
		}
		.desc {
			text-indent: 2em;
		}
	}
}
</style>
